* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  font-family: "";
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
  zoom: 90%;
}
ol,
ul {
  list-style: none;
}
a {
  color: #000;
  text-decoration: none;
}
button {
  background: none;
}
input,
textarea {
  outline: none;
}
html,
body {
  height: 100%;
}
.first_box {
  background: resolve('header-bg.png') center @(max-width:560px) resolve('header-bg-mob.png') center no-repeat;
  background-size: cover @(max-width:560px) cover;
  height: 100%;
